import React, { useState } from 'react';
import { Settings, Save, RefreshCw } from 'lucide-react';
import { Button } from '../components/ui/Button';
import { Input } from '../components/ui/Input';
import { Label } from '../components/ui/Label';

export const SettingsPage: React.FC = () => {
  const [apiKey, setApiKey] = useState('');
  const [model, setModel] = useState('mimo-v2-flash');
  const [temperature, setTemperature] = useState('0.7');

  const handleSave = () => {
    alert('');
  };

  const handleReset = () => {
    setApiKey('');
    setModel('mimo-v2-flash');
    setTemperature('0.7');
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 p-6">
      <div className="max-w-4xl mx-auto space-y-6">
        {/* Header */}
        <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-6">
          <h1 className="text-2xl font-bold text-gray-900 dark:text-gray-100 mb-2">
            
          </h1>
          <p className="text-gray-600 dark:text-gray-400">
            
          </p>
        </div>

        {/* Settings Form */}
        <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-6">
          <div className="space-y-6">
            <div>
              <Label htmlFor="apiKey">API</Label>
              <Input
                id="apiKey"
                type="password"
                value={apiKey}
                onChange={(e) => setApiKey(e.target.value)}
                placeholder="API"
                className="mt-2"
              />
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-2">
                API
              </p>
            </div>

            <div>
              <Label htmlFor="model"></Label>
              <select
                id="model"
                value={model}
                onChange={(e) => setModel(e.target.value)}
                className="mt-2 w-full px-4 py-3 border border-gray-300 dark:border-gray-700 rounded-lg bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100"
              >
                <option value="mimo-v2-flash">MIMO V2 Flash</option>
                <option value="mimo-v2-pro">MIMO V2 Pro</option>
                <option value="custom"></option>
              </select>
            </div>

            <div>
              <Label htmlFor="temperature"></Label>
              <Input
                id="temperature"
                type="number"
                min="0"
                max="2"
                step="0.1"
                value={temperature}
                onChange={(e) => setTemperature(e.target.value)}
                className="mt-2"
              />
              <p className="text-sm text-gray-500 dark:text-gray-400 mt-2">
                 (0.0-2.0)
              </p>
            </div>

            <div className="flex gap-4 pt-4">
              <Button onClick={handleSave} className="flex items-center gap-2">
                <Save className="w-4 h-4" />
                
              </Button>
              <Button variant="outline" onClick={handleReset} className="flex items-center gap-2">
                <RefreshCw className="w-4 h-4" />
                
              </Button>
            </div>
          </div>
        </div>

        {/* Info Section */}
        <div className="bg-blue-50 dark:bg-blue-900/20 border border-blue-200 dark:border-blue-800 rounded-xl p-6">
          <div className="flex items-start gap-3">
            <Settings className="w-5 h-5 text-blue-600 dark:text-blue-400 mt-0.5" />
            <div>
              <h3 className="font-semibold text-blue-900 dark:text-blue-100 mb-1">
                
              </h3>
              <p className="text-sm text-blue-800 dark:text-blue-200">
                API
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};