import React, { useState, useEffect } from 'react';
import { RefreshCw, CheckCircle, AlertCircle, Clock } from 'lucide-react';

interface SyncProgressProps {
  status: 'idle' | 'syncing' | 'completed' | 'error' | 'degraded';
  progress: number;
  message: string;
  onManualSync?: () => void;
  onRetry?: () => void;
}

export const SyncProgress: React.FC<SyncProgressProps> = ({
  status,
  progress,
  message,
  onManualSync,
  onRetry,
}) => {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    if (status !== 'idle') {
      setIsVisible(true);
    }
  }, [status]);

  const getStatusColor = () => {
    switch (status) {
      case 'syncing':
        return 'bg-blue-500';
      case 'completed':
        return 'bg-green-500';
      case 'error':
        return 'bg-red-500';
      case 'degraded':
        return 'bg-yellow-500';
      default:
        return 'bg-gray-500';
    }
  };

  const getStatusIcon = () => {
    switch (status) {
      case 'syncing':
        return <RefreshCw className="w-4 h-4 animate-spin" />;
      case 'completed':
        return <CheckCircle className="w-4 h-4" />;
      case 'error':
        return <AlertCircle className="w-4 h-4" />;
      case 'degraded':
        return <Clock className="w-4 h-4" />;
      default:
        return null;
    }
  };

  const getStatusText = () => {
    switch (status) {
      case 'syncing':
        return '...';
      case 'completed':
        return '';
      case 'error':
        return '';
      case 'degraded':
        return '';
      default:
        return '';
    }
  };

  if (!isVisible && status === 'idle') {
    return null;
  }

  return (
    <div className="fixed bottom-4 left-4 z-50">
      <div className="p-3 bg-gray-800 rounded-lg shadow-lg border border-gray-700 min-w-80">
        <div className="flex items-center justify-between mb-2">
          <div className="flex items-center space-x-2">
            <div className={`p-1 rounded ${getStatusColor()}`}>
              {getStatusIcon()}
            </div>
            <span className="text-sm font-medium text-white">
              {getStatusText()}
            </span>
          </div>
          <button
            onClick={() => setIsVisible(false)}
            className="text-gray-400 hover:text-white text-xs"
          >
            
          </button>
        </div>

        {/*  */}
        {status === 'syncing' && (
          <div className="mb-2">
            <div className="w-full bg-gray-700 rounded-full h-2">
              <div 
                className={`h-2 rounded-full ${getStatusColor()}`} 
                style={{ width: `${progress}%` }}
              />
            </div>
            <div className="text-xs text-gray-400 mt-1 text-right">
              {progress}%
            </div>
          </div>
        )}

        {/*  */}
        <div className="text-xs text-gray-300 mb-2">
          {message}
        </div>

        {/*  */}
        <div className="flex space-x-2">
          {status === 'error' && onRetry && (
            <button
              onClick={onRetry}
              className="px-2 py-1 bg-red-600 hover:bg-red-700 text-white text-xs rounded transition-colors"
            >
              
            </button>
          )}
          {status !== 'syncing' && onManualSync && (
            <button
              onClick={onManualSync}
              className="px-2 py-1 bg-blue-600 hover:bg-blue-700 text-white text-xs rounded transition-colors"
            >
              
            </button>
          )}
        </div>
      </div>
    </div>
  );
};

/**
 * 
 */
export const SyncStatusIndicator: React.FC = () => {
  const [status, setStatus] = useState<'idle' | 'syncing' | 'completed' | 'error' | 'degraded'>('idle');
  const [progress, setProgress] = useState(0);
  const [message, setMessage] = useState('');

  useEffect(() => {
    // 
    const simulateSync = () => {
      setStatus('syncing');
      setProgress(0);
      setMessage('...');

      let currentProgress = 0;
      const interval = setInterval(() => {
        currentProgress += 10;
        setProgress(currentProgress);

        if (currentProgress < 30) {
          setMessage('...');
        } else if (currentProgress < 60) {
          setMessage('...');
        } else if (currentProgress < 90) {
          setMessage('...');
        } else {
          setMessage('...');
        }

        if (currentProgress >= 100) {
          clearInterval(interval);
          setStatus('completed');
          setMessage('');
          
          // 3
          setTimeout(() => {
            setStatus('idle');
          }, 3000);
        }
      }, 200);
    };

    // 30
    const interval = setInterval(simulateSync, 30000);
    
    // 
    simulateSync();

    return () => clearInterval(interval);
  }, []);

  const handleManualSync = () => {
    setStatus('syncing');
    setProgress(0);
    setMessage('...');

    let currentProgress = 0;
    const interval = setInterval(() => {
      currentProgress += 15;
      setProgress(currentProgress);

      if (currentProgress < 40) {
        setMessage('...');
      } else if (currentProgress < 80) {
        setMessage('...');
      } else {
        setMessage('...');
      }

      if (currentProgress >= 100) {
        clearInterval(interval);
        setStatus('completed');
        setMessage('');
        
        setTimeout(() => {
          setStatus('idle');
        }, 3000);
      }
    }, 150);
  };

  const handleRetry = () => {
    handleManualSync();
  };

  return (
    <SyncProgress
      status={status}
      progress={progress}
      message={message}
      onManualSync={handleManualSync}
      onRetry={handleRetry}
    />
  );
};