import React, { useState } from 'react';
import { 
  FileText, 
  Video, 
  File as FileIcon,
  Upload,
  Settings,
  Search,
  Filter,
  MoreVertical,
  Play,
  Pause,
  CheckCircle2,
  AlertCircle
} from 'lucide-react';
import { Badge } from './ui/Badge';
import { Button } from './ui/Button';
import { Input } from './ui/Input';
import { Tabs, TabsList, TabsTrigger, TabsContent } from './ui/Tabs';
import { DropdownMenu, DropdownMenuItem } from './ui/DropdownMenu';

export const RefactoredDashboard: React.FC = () => {
  const [searchQuery, setSearchQuery] = useState('');
  const [filterType, setFilterType] = useState('all');

  const stats = [
    { label: '', value: '156', change: '+12%', trend: 'up' },
    { label: '', value: '98.5%', change: '+2.3%', trend: 'up' },
    { label: '', value: '2.3min', change: '-15%', trend: 'down' },
    { label: '', value: '8', change: '+2', trend: 'up' },
  ];

  const recentTasks = [
    { id: 'T001', type: '', status: 'processing', progress: 65, time: '2' },
    { id: 'T002', type: '', status: 'completed', progress: 100, time: '5' },
    { id: 'T003', type: '', status: 'completed', progress: 100, time: '10' },
    { id: 'T004', type: '', status: 'failed', progress: 0, time: '15' },
    { id: 'T005', type: '', status: 'pending', progress: 0, time: '20' },
  ];

  // const typeColors = {
  //   '': 'bg-green-50 text-green-600 dark:bg-green-900/20 dark:text-green-400',
  //   '': 'bg-blue-50 text-blue-600 dark:bg-blue-900/20 dark:text-blue-400',
  //   '': 'bg-purple-50 text-purple-600 dark:bg-purple-900/20 dark:text-purple-400',
  //   '': 'bg-orange-50 text-orange-600 dark:bg-orange-900/20 dark:text-orange-400',
  // };

  const statusConfig = {
    processing: { color: 'secondary', icon: Play, text: '' },
    completed: { color: 'default', icon: CheckCircle2, text: '' },
    failed: { color: 'destructive', icon: AlertCircle, text: '' },
    pending: { color: 'outline', icon: Pause, text: '' },
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 p-6">
      <div className="max-w-6xl mx-auto space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-2xl font-bold text-gray-900 dark:text-gray-100">
              
            </h1>
            <p className="text-gray-600 dark:text-gray-400">
              NarratorAI
            </p>
          </div>
          <div className="flex items-center gap-3">
            <Button className="flex items-center gap-2">
              <Upload className="w-4 h-4" />
              
            </Button>
            <Button variant="outline" className="flex items-center gap-2">
              <Settings className="w-4 h-4" />
              
            </Button>
          </div>
        </div>

        {/* Stats Grid */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          {stats.map((stat, index) => (
            <div
              key={index}
              className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-5"
            >
              <div className="text-sm text-gray-600 dark:text-gray-400 mb-1">
                {stat.label}
              </div>
              <div className="text-2xl font-bold text-gray-900 dark:text-gray-100 mb-1">
                {stat.value}
              </div>
              <div className={`text-sm ${stat.trend === 'up' ? 'text-green-600' : 'text-red-600'}`}>
                {stat.change}
              </div>
            </div>
          ))}
        </div>

        {/* Search and Filter */}
        <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-4">
          <div className="flex items-center gap-4">
            <div className="flex-1 relative">
              <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-gray-400" />
              <Input
                placeholder="..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                className="pl-10"
              />
            </div>
            <div className="flex items-center gap-2">
              <Filter className="w-4 h-4 text-gray-400" />
              <select
                value={filterType}
                onChange={(e) => setFilterType(e.target.value)}
                className="px-3 py-2 border border-gray-300 dark:border-gray-700 rounded-lg bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100 text-sm"
              >
                <option value="all"></option>
                <option value="video"></option>
                <option value="text"></option>
                <option value="subtitle"></option>
              </select>
            </div>
          </div>
        </div>

        {/* Tabs */}
        <Tabs value="recent" onValueChange={() => {}}>
          <TabsList>
            <TabsTrigger value="recent"></TabsTrigger>
            <TabsTrigger value="processing"></TabsTrigger>
            <TabsTrigger value="completed"></TabsTrigger>
            <TabsTrigger value="failed"></TabsTrigger>
          </TabsList>

          <TabsContent value="recent">
            <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 overflow-hidden">
              <table className="w-full text-sm">
                <thead className="bg-gray-50 dark:bg-gray-800">
                  <tr>
                    <th className="px-4 py-3 text-left font-medium text-gray-700 dark:text-gray-300">ID</th>
                    <th className="px-4 py-3 text-left font-medium text-gray-700 dark:text-gray-300"></th>
                    <th className="px-4 py-3 text-left font-medium text-gray-700 dark:text-gray-300"></th>
                    <th className="px-4 py-3 text-left font-medium text-gray-700 dark:text-gray-300"></th>
                    <th className="px-4 py-3 text-left font-medium text-gray-700 dark:text-gray-300"></th>
                    <th className="px-4 py-3 text-left font-medium text-gray-700 dark:text-gray-300"></th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200 dark:divide-gray-800">
                  {recentTasks.map((task) => {
                    const status = statusConfig[task.status as keyof typeof statusConfig];
                    const TypeIcon = task.type.includes('') ? Video : task.type.includes('') ? FileText : FileIcon;
                    
                    return (
                      <tr key={task.id} className="hover:bg-gray-50 dark:hover:bg-gray-800/50">
                        <td className="px-4 py-3 font-medium text-gray-900 dark:text-gray-100">{task.id}</td>
                        <td className="px-4 py-3">
                          <div className="flex items-center gap-2">
                            <TypeIcon className="w-4 h-4 text-gray-500" />
                            <span className="text-gray-900 dark:text-gray-100">{task.type}</span>
                          </div>
                        </td>
                        <td className="px-4 py-3">
                          <Badge variant={status.color as any}>
                            {status.text}
                          </Badge>
                        </td>
                        <td className="px-4 py-3">
                          <div className="flex items-center gap-2">
                            <div className="w-24 h-2 bg-gray-200 dark:bg-gray-800 rounded-full overflow-hidden">
                              <div
                                className="h-full bg-primary-600 transition-all duration-300"
                                style={{ width: `${task.progress}%` }}
                              />
                            </div>
                            <span className="text-xs text-gray-600 dark:text-gray-400">
                              {task.progress}%
                            </span>
                          </div>
                        </td>
                        <td className="px-4 py-3 text-gray-600 dark:text-gray-400">{task.time}</td>
                        <td className="px-4 py-3">
                          <DropdownMenu
                            trigger={
                              <button className="p-1 hover:bg-gray-100 dark:hover:bg-gray-800 rounded">
                                <MoreVertical className="w-4 h-4 text-gray-500" />
                              </button>
                            }
                          >
                            <DropdownMenuItem></DropdownMenuItem>
                            <DropdownMenuItem></DropdownMenuItem>
                            <DropdownMenuItem></DropdownMenuItem>
                            <DropdownMenuItem></DropdownMenuItem>
                          </DropdownMenu>
                        </td>
                      </tr>
                    );
                  })}
                </tbody>
              </table>
            </div>
          </TabsContent>

          <TabsContent value="processing">
            <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-6">
              <p className="text-gray-600 dark:text-gray-400">...</p>
            </div>
          </TabsContent>

          <TabsContent value="completed">
            <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-6">
              <p className="text-gray-600 dark:text-gray-400">...</p>
            </div>
          </TabsContent>

          <TabsContent value="failed">
            <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-6">
              <p className="text-gray-600 dark:text-gray-400">...</p>
            </div>
          </TabsContent>
        </Tabs>

        {/* Quick Actions */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-5 hover:shadow-md transition-shadow cursor-pointer">
            <div className="w-10 h-10 bg-blue-50 text-blue-600 rounded-lg flex items-center justify-center mb-3">
              <FileText className="w-5 h-5" />
            </div>
            <h3 className="font-semibold text-gray-900 dark:text-gray-100 mb-1"></h3>
            <p className="text-sm text-gray-600 dark:text-gray-400 mb-3"></p>
            <Button variant="outline" size="sm" className="w-full"></Button>
          </div>

          <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-5 hover:shadow-md transition-shadow cursor-pointer">
            <div className="w-10 h-10 bg-green-50 text-green-600 rounded-lg flex items-center justify-center mb-3">
              <Video className="w-5 h-5" />
            </div>
            <h3 className="font-semibold text-gray-900 dark:text-gray-100 mb-1"></h3>
            <p className="text-sm text-gray-600 dark:text-gray-400 mb-3"></p>
            <Button variant="outline" size="sm" className="w-full"></Button>
          </div>

          <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-5 hover:shadow-md transition-shadow cursor-pointer">
            <div className="w-10 h-10 bg-purple-50 text-purple-600 rounded-lg flex items-center justify-center mb-3">
              <FileIcon className="w-5 h-5" />
            </div>
            <h3 className="font-semibold text-gray-900 dark:text-gray-100 mb-1"></h3>
            <p className="text-sm text-gray-600 dark:text-gray-400 mb-3"></p>
            <Button variant="outline" size="sm" className="w-full"></Button>
          </div>
        </div>
      </div>
    </div>
  );
};