import React, { useState } from 'react';
import { RefreshCw, Cloud, CheckCircle, AlertCircle } from 'lucide-react';
import { getPerformanceMonitor } from '../utils/PerformanceMonitor';
import { apiFsm } from '../utils/ApiFileSystemStateMachine';

interface ManualSyncButtonProps {
  onSyncStart?: () => void;
  onSyncComplete?: (success: boolean) => void;
  onSyncError?: (error: string) => void;
}

export const ManualSyncButton: React.FC<ManualSyncButtonProps> = ({
  onSyncStart,
  onSyncComplete,
  onSyncError,
}) => {
  const [isSyncing, setIsSyncing] = useState(false);
  const [syncStatus, setSyncStatus] = useState<'idle' | 'syncing' | 'completed' | 'error'>('idle');
  const [syncMessage, setSyncMessage] = useState('');

  const handleManualSync = async () => {
    if (isSyncing) return;

    setIsSyncing(true);
    setSyncStatus('syncing');
    setSyncMessage('...');
    
    if (onSyncStart) onSyncStart();

    try {
      // 
      const steps = [
        { progress: 10, message: ' API ...' },
        { progress: 30, message: '...' },
        { progress: 50, message: ' API...' },
        { progress: 70, message: '...' },
        { progress: 90, message: '...' },
        { progress: 100, message: '' },
      ];

      for (const step of steps) {
        setSyncMessage(step.message);
        
        //  API 
        await new Promise(resolve => setTimeout(resolve, 500));
        
        //  API 
        const isApiAvailable = apiFsm.isApiAvailable();
        
        if (!isApiAvailable && step.progress > 30) {
          throw new Error('API ');
        }
      }

      // 
      const monitor = getPerformanceMonitor();
      monitor.recordMetrics({
        apiLatency: 100,
        apiStatus: 'connected',
        errorRate: 0,
        requestCount: 1,
        successCount: 1,
        failureCount: 0,
      });

      setSyncStatus('completed');
      setSyncMessage('');
      
      if (onSyncComplete) onSyncComplete(true);

      // 3
      setTimeout(() => {
        setIsSyncing(false);
        setSyncStatus('idle');
        setSyncMessage('');
      }, 3000);

    } catch (error: any) {
      setSyncStatus('error');
      setSyncMessage(`: ${error.message}`);
      
      // 
      const monitor = getPerformanceMonitor();
      monitor.recordMetrics({
        apiLatency: 0,
        apiStatus: 'disconnected',
        errorRate: 1,
        requestCount: 1,
        successCount: 0,
        failureCount: 1,
      });

      if (onSyncError) onSyncError(error.message);

      // 5
      setTimeout(() => {
        setIsSyncing(false);
        setSyncStatus('idle');
        setSyncMessage('');
      }, 5000);
    }
  };

  const getButtonColor = () => {
    switch (syncStatus) {
      case 'syncing':
        return 'bg-blue-600 hover:bg-blue-700';
      case 'completed':
        return 'bg-green-600 hover:bg-green-700';
      case 'error':
        return 'bg-red-600 hover:bg-red-700';
      default:
        return 'bg-blue-600 hover:bg-blue-700';
    }
  };

  const getButtonIcon = () => {
    if (isSyncing) {
      return <RefreshCw className="w-4 h-4 animate-spin" />;
    }
    switch (syncStatus) {
      case 'completed':
        return <CheckCircle className="w-4 h-4" />;
      case 'error':
        return <AlertCircle className="w-4 h-4" />;
      default:
        return <RefreshCw className="w-4 h-4" />;
    }
  };

  return (
    <div className="flex items-center space-x-2">
      <button
        onClick={handleManualSync}
        disabled={isSyncing}
        className={`flex items-center space-x-2 px-4 py-2 ${getButtonColor()} text-white rounded-lg transition-colors disabled:opacity-50 disabled:cursor-not-allowed`}
      >
        {getButtonIcon()}
        <span>
          {isSyncing ? '...' : ''}
        </span>
      </button>
      
      {syncMessage && (
        <span className="text-xs text-gray-400">
          {syncMessage}
        </span>
      )}
    </div>
  );
};

/**
 * 
 */
export const DataSyncStatus: React.FC = () => {
  const [syncState, setSyncState] = useState<{
    status: 'idle' | 'syncing' | 'completed' | 'error' | 'degraded';
    lastSync: string | null;
    pendingCount: number;
  }>({
    status: 'idle',
    lastSync: null,
    pendingCount: 0,
  });

  const checkSyncStatus = async () => {
    try {
      const isApiAvailable = apiFsm.isApiAvailable();
      
      if (!isApiAvailable) {
        setSyncState(prev => ({
          ...prev,
          status: 'degraded',
          pendingCount: prev.pendingCount + 1,
        }));
        return;
      }

      // 
      const pendingData = Math.floor(Math.random() * 5);
      
      if (pendingData > 0) {
        setSyncState(prev => ({
          ...prev,
          status: 'syncing',
          pendingCount: pendingData,
        }));
      } else {
        setSyncState(prev => ({
          ...prev,
          status: 'completed',
          lastSync: new Date().toISOString(),
        }));
      }
    } catch (error) {
      setSyncState(prev => ({
        ...prev,
        status: 'error',
      }));
    }
  };

  React.useEffect(() => {
    checkSyncStatus();
    const interval = setInterval(checkSyncStatus, 30000);
    return () => clearInterval(interval);
  }, []);

  const getStatusColor = () => {
    switch (syncState.status) {
      case 'syncing':
        return 'text-blue-400';
      case 'completed':
        return 'text-green-400';
      case 'error':
        return 'text-red-400';
      case 'degraded':
        return 'text-yellow-400';
      default:
        return 'text-gray-400';
    }
  };

  const getStatusText = () => {
    switch (syncState.status) {
      case 'syncing':
        return ` (${syncState.pendingCount} )`;
      case 'completed':
        return '';
      case 'error':
        return '';
      case 'degraded':
        return '';
      default:
        return '';
    }
  };

  return (
    <div className="p-3 bg-gray-800 rounded-lg">
      <div className="flex items-center justify-between mb-2">
        <div className="flex items-center space-x-2">
          <Cloud className="w-4 h-4 text-gray-400" />
          <span className="text-sm font-medium text-white"></span>
        </div>
        <span className={`text-xs font-medium ${getStatusColor()}`}>
          {getStatusText()}
        </span>
      </div>
      
      <div className="text-xs text-gray-400 space-y-1">
        <div>
          : {syncState.lastSync 
            ? new Date(syncState.lastSync).toLocaleString('zh-CN') 
            : ''}
        </div>
        <div>
          : {syncState.pendingCount}
        </div>
      </div>

      <div className="mt-2">
        <ManualSyncButton />
      </div>
    </div>
  );
};