import React from 'react';
import { 
  HelpCircle, 
  Book, 
  Zap,
  Shield,
  Globe,
  Mail,
  ExternalLink
} from 'lucide-react';

export const HelpPage: React.FC = () => {
  const faqs = [
    {
      question: '',
      answer: '',
    },
    {
      question: '',
      answer: '',
    },
    {
      question: '',
      answer: '12-3',
    },
    {
      question: '',
      answer: '',
    },
  ];

  const features = [
    {
      icon: Book,
      title: '',
      description: 'API',
      link: '#',
    },
    {
      icon: Zap,
      title: '',
      description: '5',
      link: '#',
    },
    {
      icon: Shield,
      title: '',
      description: '',
      link: '#',
    },
    {
      icon: Globe,
      title: '',
      description: '',
      link: '#',
    },
  ];

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 p-6">
      <div className="max-w-4xl mx-auto space-y-6">
        {/* Header */}
        <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-6">
          <h1 className="text-2xl font-bold text-gray-900 dark:text-gray-100 mb-2">
            
          </h1>
          <p className="text-gray-600 dark:text-gray-400">
            
          </p>
        </div>

        {/* Quick Links */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {features.map((feature, index) => {
            const Icon = feature.icon;
            
            return (
              <a
                key={index}
                href={feature.link}
                className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-5 hover:shadow-md transition-shadow"
              >
                <div className="w-10 h-10 bg-primary-50 text-primary-600 rounded-lg flex items-center justify-center mb-3">
                  <Icon className="w-5 h-5" />
                </div>
                <h3 className="font-semibold text-gray-900 dark:text-gray-100 mb-1">
                  {feature.title}
                </h3>
                <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                  {feature.description}
                </p>
                <span className="text-sm text-primary-600 dark:text-primary-400 flex items-center gap-1">
                   <ExternalLink className="w-3 h-3" />
                </span>
              </a>
            );
          })}
        </div>

        {/* FAQs */}
        <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-6">
          <h2 className="text-xl font-bold text-gray-900 dark:text-gray-100 mb-6 flex items-center gap-2">
            <HelpCircle className="w-5 h-5 text-primary-600" />
            
          </h2>
          <div className="space-y-4">
            {faqs.map((faq, index) => (
              <div key={index} className="border-b border-gray-200 dark:border-gray-800 pb-4 last:border-0 last:pb-0">
                <h3 className="font-semibold text-gray-900 dark:text-gray-100 mb-2">
                  {faq.question}
                </h3>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  {faq.answer}
                </p>
              </div>
            ))}
          </div>
        </div>

        {/* Contact */}
        <div className="bg-white dark:bg-gray-950 rounded-xl border border-gray-200 dark:border-gray-800 p-6">
          <h2 className="text-xl font-bold text-gray-900 dark:text-gray-100 mb-6 flex items-center gap-2">
            <Mail className="w-5 h-5 text-primary-600" />
            
          </h2>
          <div className="space-y-4">
            <div>
              <h3 className="font-medium text-gray-900 dark:text-gray-100 mb-1">
                
              </h3>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                
              </p>
            </div>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div className="bg-gray-50 dark:bg-gray-900 rounded-lg p-4">
                <h4 className="font-medium text-gray-900 dark:text-gray-100 mb-1">
                  
                </h4>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  support@translator-agent.com
                </p>
              </div>
              <div className="bg-gray-50 dark:bg-gray-900 rounded-lg p-4">
                <h4 className="font-medium text-gray-900 dark:text-gray-100 mb-1">
                  
                </h4>
                <p className="text-sm text-gray-600 dark:text-gray-400">
                  translator-agent.community
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};