import React, { useState, useEffect, useCallback } from 'react';
import { 
  Loader2, 
  Activity, 
  Clock, 
  CheckCircle2, 
  AlertCircle,
  FileText,
  Video,
  Type
} from 'lucide-react';
import { FileSystemStateMachine, TaskState, TaskStatus } from '../utils/FileSystemStateMachine';

interface RealTimeProgressMonitorProps {
  taskId: string;
  module: string;
  onProgressUpdate?: (progress: number, message: string) => void;
}

interface ProgressStep {
  id: string;
  name: string;
  icon: React.ReactNode;
  status: 'pending' | 'active' | 'completed' | 'error';
  progress: number;
  message: string;
}

export const RealTimeProgressMonitor: React.FC<RealTimeProgressMonitorProps> = ({
  taskId,
  module,
  onProgressUpdate
}) => {
  const [taskState, setTaskState] = useState<TaskState | null>(null);
  const [fsm, setFsm] = useState<FileSystemStateMachine | null>(null);
  const [loading, setLoading] = useState(true);
  const [steps, setSteps] = useState<ProgressStep[]>([]);

  // 
  useEffect(() => {
    const initializeFSM = async () => {
      try {
        const fsmInstance = new FileSystemStateMachine();
        await fsmInstance.initialize();
        setFsm(fsmInstance);
      } catch (error) {
        console.error('[RealTimeProgressMonitor] Failed to initialize FSM:', error);
      }
    };

    initializeFSM();
  }, []);

  // 
  const loadTaskState = useCallback(async () => {
    if (!fsm) return;

    try {
      const state = await fsm.readTaskState(taskId, module);
      setTaskState(state);
      
      // 
      if (onProgressUpdate) {
        onProgressUpdate(state.progress.percentage, state.progress.message);
      }

      // 
      updateSteps(state);
    } catch (error) {
      console.error(`[RealTimeProgressMonitor] Failed to load task state:`, error);
    } finally {
      setLoading(false);
    }
  }, [fsm, taskId, module, onProgressUpdate]);

  // 
  const updateSteps = useCallback((state: TaskState) => {
    const progress = state.progress.percentage;
    
    const newSteps: ProgressStep[] = [
      {
        id: 'upload',
        name: '',
        icon: <FileText className="w-4 h-4" />,
        status: progress >= 10 ? 'completed' : progress > 0 ? 'active' : 'pending',
        progress: Math.min(progress * 10, 100),
        message: progress >= 10 ? '' : '...'
      },
      {
        id: 'process',
        name: '',
        icon: <Video className="w-4 h-4" />,
        status: progress >= 50 ? 'completed' : progress > 10 ? 'active' : 'pending',
        progress: progress > 10 ? Math.min((progress - 10) * 2, 100) : 0,
        message: progress >= 50 ? '' : progress > 10 ? '...' : '...'
      },
      {
        id: 'translate',
        name: '',
        icon: <Type className="w-4 h-4" />,
        status: progress >= 90 ? 'completed' : progress > 50 ? 'active' : 'pending',
        progress: progress > 50 ? Math.min((progress - 50) * 2.5, 100) : 0,
        message: progress >= 90 ? '' : progress > 50 ? '...' : '...'
      },
      {
        id: 'complete',
        name: '',
        icon: <CheckCircle2 className="w-4 h-4" />,
        status: progress >= 100 ? 'completed' : progress > 90 ? 'active' : 'pending',
        progress: progress > 90 ? Math.min((progress - 90) * 10, 100) : 0,
        message: progress >= 100 ? '' : '...'
      }
    ];

    setSteps(newSteps);
  }, []);

  // 
  useEffect(() => {
    if (!fsm || !taskId || !module) return;

    // 
    loadTaskState();

    // 2
    const interval = setInterval(() => {
      loadTaskState();
    }, 2000);

    return () => {
      if (interval) {
        clearInterval(interval);
      }
    };
  }, [fsm, taskId, module, loadTaskState]);

  // 
  const getStatusIcon = (status: TaskStatus) => {
    switch (status) {
      case TaskStatus.SUCCESS:
        return <CheckCircle2 className="w-5 h-5 text-green-500" />;
      case TaskStatus.IN_PROGRESS:
        return <Loader2 className="w-5 h-5 text-blue-500 animate-spin" />;
      case TaskStatus.ERROR:
        return <AlertCircle className="w-5 h-5 text-red-500" />;
      case TaskStatus.PENDING:
        return <Clock className="w-5 h-5 text-yellow-500" />;
      case TaskStatus.PAUSED:
        return <Activity className="w-5 h-5 text-gray-500" />;
      default:
        return <Activity className="w-5 h-5 text-gray-500" />;
    }
  };

  // 
  const getStatusText = (status: TaskStatus) => {
    switch (status) {
      case TaskStatus.SUCCESS:
        return '';
      case TaskStatus.IN_PROGRESS:
        return '';
      case TaskStatus.ERROR:
        return '';
      case TaskStatus.PENDING:
        return '';
      case TaskStatus.PAUSED:
        return '';
      default:
        return '';
    }
  };

  // 
  const getStepStatusStyle = (status: 'pending' | 'active' | 'completed' | 'error') => {
    switch (status) {
      case 'completed':
        return 'bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-300';
      case 'active':
        return 'bg-blue-100 text-blue-700 dark:bg-blue-900/30 dark:text-blue-300';
      case 'error':
        return 'bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-300';
      case 'pending':
        return 'bg-gray-100 text-gray-600 dark:bg-gray-800 dark:text-gray-400';
      default:
        return 'bg-gray-100 text-gray-600 dark:bg-gray-800 dark:text-gray-400';
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center p-4">
        <Loader2 className="w-6 h-6 animate-spin text-blue-500" />
        <span className="ml-2 text-gray-600 dark:text-gray-400">...</span>
      </div>
    );
  }

  if (!taskState) {
    return (
      <div className="flex items-center justify-center p-4 text-gray-500 dark:text-gray-400">
        <AlertCircle className="w-5 h-5 mr-2" />
        
      </div>
    );
  }

  return (
    <div className="bg-white dark:bg-gray-950 border border-gray-200 dark:border-gray-800 rounded-lg p-4">
      {/*  */}
      <div className="flex items-center justify-between mb-4">
        <div className="flex items-center gap-2">
          {getStatusIcon(taskState.status)}
          <div>
            <div className="font-semibold text-gray-900 dark:text-gray-100">
              {getStatusText(taskState.status)}
            </div>
            <div className="text-xs text-gray-500 dark:text-gray-400">
              ID: {taskState.taskId.substr(0, 12)}...
            </div>
          </div>
        </div>
        <div className="text-right">
          <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
            {taskState.progress.percentage}%
          </div>
          <div className="text-xs text-gray-500 dark:text-gray-400">
            {new Date(taskState.progress.timestamp).toLocaleTimeString('zh-CN')}
          </div>
        </div>
      </div>

      {/*  */}
      <div className="mb-4">
        <div className="h-2 bg-gray-200 dark:bg-gray-800 rounded-full overflow-hidden">
          <div 
            className="h-full bg-blue-600 transition-all duration-500 ease-out"
            style={{ width: `${taskState.progress.percentage}%` }}
          />
        </div>
        <div className="text-sm text-gray-600 dark:text-gray-400 mt-1">
          {taskState.progress.message}
        </div>
      </div>

      {/*  */}
      <div className="space-y-3">
        <div className="text-sm font-medium text-gray-700 dark:text-gray-300">
          
        </div>
        {steps.map((step) => (
          <div key={step.id} className="flex items-center gap-3">
            <div className={`p-1.5 rounded ${getStepStatusStyle(step.status)}`}>
              {step.icon}
            </div>
            <div className="flex-1">
              <div className="flex items-center justify-between">
                <span className="text-sm font-medium text-gray-900 dark:text-gray-100">
                  {step.name}
                </span>
                <span className="text-xs text-gray-500 dark:text-gray-400">
                  {step.progress}%
                </span>
              </div>
              {step.status === 'active' && (
                <div className="h-1.5 bg-gray-200 dark:bg-gray-800 rounded-full overflow-hidden mt-1">
                  <div 
                    className="h-full bg-blue-500 transition-all duration-300"
                    style={{ width: `${step.progress}%` }}
                  />
                </div>
              )}
              <div className="text-xs text-gray-500 dark:text-gray-400 mt-0.5">
                {step.message}
              </div>
            </div>
          </div>
        ))}
      </div>

      {/*  */}
      {taskState.files && (
        <div className="mt-4 pt-4 border-t border-gray-200 dark:border-gray-800">
          <div className="grid grid-cols-3 gap-2 text-center">
            <div className="p-2 bg-gray-50 dark:bg-gray-900 rounded">
              <div className="text-lg font-bold text-gray-900 dark:text-gray-100">
                {taskState.files.uploaded.length}
              </div>
              <div className="text-xs text-gray-500 dark:text-gray-400"></div>
            </div>
            <div className="p-2 bg-green-50 dark:bg-green-900/20 rounded">
              <div className="text-lg font-bold text-green-600 dark:text-green-400">
                {taskState.files.processed.length}
              </div>
              <div className="text-xs text-gray-500 dark:text-gray-400"></div>
            </div>
            <div className="p-2 bg-red-50 dark:bg-red-900/20 rounded">
              <div className="text-lg font-bold text-red-600 dark:text-red-400">
                {taskState.files.failed.length}
              </div>
              <div className="text-xs text-gray-500 dark:text-gray-400"></div>
            </div>
          </div>
        </div>
      )}

      {/*  */}
      {taskState.memoryLayer && (
        <div className="mt-4 pt-4 border-t border-gray-200 dark:border-gray-800">
          <div className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
            
          </div>
          <div className="grid grid-cols-3 gap-2 text-center">
            <div className="p-2 bg-blue-50 dark:bg-blue-900/20 rounded">
              <div className="text-lg font-bold text-blue-600 dark:text-blue-400">
                {taskState.memoryLayer.conversationHistory.length}
              </div>
              <div className="text-xs text-gray-500 dark:text-gray-400"></div>
            </div>
            <div className="p-2 bg-purple-50 dark:bg-purple-900/20 rounded">
              <div className="text-lg font-bold text-purple-600 dark:text-purple-400">
                {taskState.memoryLayer.processingResults.length}
              </div>
              <div className="text-xs text-gray-500 dark:text-gray-400"></div>
            </div>
            <div className="p-2 bg-orange-50 dark:bg-orange-900/20 rounded">
              <div className="text-lg font-bold text-orange-600 dark:text-orange-400">
                {taskState.memoryLayer.intermediateData.length}
              </div>
              <div className="text-xs text-gray-500 dark:text-gray-400"></div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};